<template lang="pug">
  div(class="home-root")
    //- div(id="PlanetScene")
    user-panel(
      :user="user"
      :planet="planet"
      :mode="'Home'"
      @openModal="openModal")
    h2 {{planet.name}}
      button(@click="openModal('planet-info')" class="detail")
    span ({{planet.positionX}},{{planet.positionY}})
    router-link(tag="button" to="/game" class="enter button-default" @click.native="lauchBootScene") 進入星球
    modal-card(v-if="isModalOpen" @close="isModalOpen = !isModalOpen")
      planet-info(
        v-if="currentOpen === 'planet-info'"
        slot="card"
        :user="user"
        :planet="planet")
      user-profile(v-if="currentOpen === 'user-profile'" slot="card" :user="user")
</template>

<script>
import UserPanel from '@/components/user/UserPanel.vue'
import ModalCard from '@/components/ModalCard.vue'
import PlanetInfo from '@/components/home/PlanetInfo.vue'
import UserProfile from '@/components/user/UserProfile.vue'
// import Phaser from 'phaser'
import { PlanetGameFunc } from '@/game/scences/PlanetScene'
import { mapState } from 'vuex'

export default {
  components: {
    UserPanel,
    ModalCard,
    PlanetInfo,
    UserProfile
  },
  data: function () {
    return {
      isModalOpen: false,
      currentOpen: ''
    }
  },
  methods: {
    openModal (one) {
      this.isModalOpen = true
      this.currentOpen = one
    },
    lauchBootScene () {
      PlanetGameFunc.ChangeScene()
      import('@/game/scences/PlayScene').then(async (module) => {
        module.getBuildingMap(this.planet.scale, this.planet.map)
      })
    }
  },
  mounted () {
    // eslint-disable-next-line
    //First time enter the game
    // var config = {
    //   // type: Phaser.AUTO,
    //   type: Phaser.WEBGL,
    //   parent: 'PlanetScene',
    //   width: window.innerWidth,
    //   height: window.innerHeight,
    //   scale: {
    //     mode: Phaser.Scale.FIT,
    //     autoCenter: Phaser.Scale.CENTER_BOTH
    //   },
    //   scene: PlanetScene
    // }
    // if (!PlanetGameFunc.game) { PlanetGameFunc.game = new Phaser.Game(config) }
  },
  computed: {
    ...mapState('user', {
      user: state => state
    }),
    ...mapState('planet', {
      planet: state => state.planet
    })
  }
  // destroyed() {
  //   // PlanetGameFunc.RemoveScene()
  // }
}
</script>

<style lang="scss" scoped>
@import "@/assets/scss/variables.scss";

.home-root {
  height: 100%;
  display: grid;
  grid-template-columns: 1fr;
  grid-template-rows: 91px 360px 30px 6px 11px 40px 43px 86px;
  grid-template-areas:
    "user-info"
    "."
    "planetname"
    "."
    "coordinate"
    "."
    "enter"
    ".";
  #PlanetScene {
    position: absolute;
    width: 100%;
    height: 100%;
    z-index: -1;
  }
  > h2 {
    grid-area: planetname;
    position: relative;
    .detail {
      background-image: url("~@/assets/icon/planet-Info.png");
      width: 11px;
      height: 11px;
      position: absolute;
      top: -5px;
      border-radius: 2px;
      background-color: transparent;
      border: none;
      padding: 0;
    }
  }
  > span {
    font-size: 10px;
    grid-area: coordinate;
    z-index: 1;
  }
  .enter {
    grid-area: enter;
    justify-self: center;
    width: 162px;
    height: 42px;
    font-size: 18px;
    z-index: 1;
  }
}
</style>
