<template lang="pug">
  div(class="planet-info-root")
    span {{user.username}}'s
    h1 {{planet.name}}
    div
      img(:src="planet.image")
    div(class="brief")
      div(class="heading-default")
        h4 簡述
      p 屬於赫克斯星體
      div(class="buff")
        div
          i
          span 稀有礦產
        div
          i
          span 建造數度+10%
    div(class="evaluation")
      div(class="heading-default")
        h4 星球評比
      div(class="evaluation-grid")
        div(class="planet-level")
          i
          span 星球等級
          span L
        div(class="user-chart")
          develop-chart
        div(class="attribute-judge")
</template>

<script>
import DevelopChart from '@/components/home/DevelopChart.vue'

export default {
  components: {
    DevelopChart
  },
  props: {
    user: Object,
    planet: Object
  }
}
</script>

<style lang="scss" scoped>
@import "@/assets/scss/variables.scss";
.planet-info-root {
  > span:nth-child(1) {
    font-size: 12px;
  }
  > h1:nth-child(2) {
    font-size: 30px;
    line-height: 30px;
    margin: 7px 0px 10px 0;
  }
  > div:nth-child(3) {
    border-top: 1px solid map-get($colors, primary);
    > img {
      width: 128px;
      height: 128px;
      margin: 34px 0 20px 0;
    }
  }
  > div.brief {
    text-align: left;
    padding-bottom: 22px;
    > p {
      box-sizing: border-box;
      height: 134px;
      padding: 12px 11px;
      font-size: 12px;
    }
    > .buff {
      display: flex;
      justify-content: space-around;
      > div {
        width: 120px;
        height: 25px;
        border: 1px solid #d182d8;
        border-radius: 8px;
        font-size: 10px;
        display: flex;
        align-items: center;
      }
    }
  }
  > div.evaluation {
    text-align: left;
    padding-bottom: 22px;
    > .evaluation-grid {
      display: grid;
      grid-template-rows: 20px 20px 17px 186px 35px;
      grid-template-columns: 255px;
      justify-content: center;
      grid-template-areas:
        "."
        "level"
        "."
        "chart"
        "attribute";
      > .planet-level {
        grid-area: level;
        font-size: 12px;
        color: map-get($colors, primary);
        border-bottom: 1px solid map-get($colors, primary);
        display: grid;
        grid-template-columns: 17px 10px 50px 148px 8px 27px;
        grid-template-rows: 1fr;
        grid-template-areas: "icon . word . L .";
        > i:nth-child(1) {
          grid-area: icon;
          background-image: url('~@/assets/icon/level-sword.png');
        }
        > span:nth-child(2) {
          grid-area: word;
        }
        > span:nth-child(3) {
          grid-area: L;
        }
      }
      > .user-chart {
        grid-area: chart;
      }
      > .attribute-judge {
        grid-area: attribute;
      }
    }
  }
}

h4 {
  font-size: 18px;
  line-height: 18px;
  padding: 7px 10px;
}
</style>
